<template>
  <div :class="[Open ? 'open' : '', 'infomation']">
    <div class="title">
      <font-awesome-icon
        :icon="['fas', 'angle-right']"
        @click="onClick()"
        v-show="!Open"
        class="icon"
      />
      <font-awesome-icon
        :icon="['fas', 'angle-down']"
        @click="onClick()"
        v-show="Open"
        class="icon"
      />
      <div class="text">
        {{ infomation.title }}
      </div>
    </div>
    <p v-show="Open">{{ infomation.content }}</p>
  </div>
</template>

<script>
export default {
  name: "Infomation",
  props: {
    infomation: Object,
  },
  data() {
    return {
      Open: false,
    };
  },
  created() {
    this.Open = false;
  },
  methods: {
    onClick() {
      this.Open = !this.Open;
      console.log(this.Open);
    },
  },
};
</script>

<style scoped>
.infomation {
  background: #aaa;
  padding: 10px 0 10px 0;
  transition: all 1s ease;
}
.infomation.open {
  border-left: 20px solid #f5deb3;
}
.title {
  color: #f5deb3;
  margin: 10px 0 7px 0;
}
.title .icon {
  font-size: 50px;
  width: 5%;
}
.title .text {
  display: inline-block;
  text-align: center;
  width: 95%;
  font-size: 50px;
  font-weight: 500;
}
p {
  background: #ddd;
  font-size: 40px;
  text-align: center;
}
</style>
